.scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
  transition: opacity 0.4s;
  opacity: 0;
}

.scroll-to-top.show {
  opacity: 1;
}

.scroll-to-top button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  color: #ffffff;
  border: 1px solid white;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 1.5em;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}
.scroll-to-top button:hover {
  background-color: #61dafb;
  transform: scale(1.1);
}

.scroll-to-top button:focus {
  outline: none;
}

@media screen and (max-width: 768px) {
  .scroll-to-top {
    bottom: 100px;
    right: 10px;
    z-index: 1000;
    transition: opacity 0.4s;
    opacity: 0;
  }
}
.scroll-to-top button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  color: #ffffff;
  border: 1px solid white;
  width: 50px;
  height: 50px;
  border-radius: 50%;

  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}
